'use client';

import { mockMaintenanceRecords, mockVehicles } from '@/data/mockData';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import {
  Activity,
  Wrench,
  DollarSign,
  Calendar
} from 'lucide-react';

const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun'];

export default function ReportsPage() {
  const totalRecords = mockMaintenanceRecords.length;
  const totalCost = mockMaintenanceRecords.reduce((acc, record) => acc + (record.cost || 0), 0);
  const totalVehicles = mockVehicles.length;

  const maintenanceByType = mockMaintenanceRecords.reduce((acc, record) => {
    acc[record.type] = (acc[record.type] || 0) + 1;
    return acc;
  }, {} as Record<string, number>);

  const monthlyActivity = months.map((month, index) => {
    const value = Math.max(2, Math.round((totalRecords + index * 3) % 12));
    return { month, value };
  });

  return (
    <div className="min-h-screen fade-in">
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-white">Operational Reports</h1>
          <p className="mt-1 text-sm text-slate-300">
            Analytics, maintenance trends, and cost summaries
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Total Vehicles</CardTitle>
              <Activity className="h-4 w-4 text-cyan-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-white">{totalVehicles}</div>
              <p className="text-xs text-slate-300">Fleet size</p>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Work Orders</CardTitle>
              <Wrench className="h-4 w-4 text-emerald-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-emerald-200">{totalRecords}</div>
              <p className="text-xs text-slate-300">Maintenance records</p>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Maintenance Cost</CardTitle>
              <DollarSign className="h-4 w-4 text-amber-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-amber-200">QAR {totalCost.toLocaleString()}</div>
              <p className="text-xs text-slate-300">Year-to-date spend</p>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Report Cycle</CardTitle>
              <Calendar className="h-4 w-4 text-blue-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-white">Monthly</div>
              <p className="text-xs text-slate-300">Current reporting period</p>
            </CardContent>
          </Card>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <Card className="h-full">
            <CardHeader>
              <CardTitle className="text-lg">Maintenance Activity (Last 6 Months)</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {monthlyActivity.map((entry) => (
                  <div key={entry.month} className="flex items-center gap-3">
                    <span className="w-8 text-sm text-slate-300">{entry.month}</span>
                    <div className="flex-1 h-3 rounded-full bg-white/10 overflow-hidden">
                      <div
                        className="h-full rounded-full bg-gradient-to-r from-blue-500 to-emerald-400"
                        style={{ width: `${entry.value * 8}%` }}
                      />
                    </div>
                    <span className="text-sm text-slate-300">{entry.value} jobs</span>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          <Card className="h-full">
            <CardHeader>
              <CardTitle className="text-lg">Work Orders by Type</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {Object.entries(maintenanceByType).map(([type, count]) => (
                  <div key={type} className="flex items-center justify-between">
                    <span className="text-sm text-slate-300 capitalize">{type}</span>
                    <Badge variant="secondary">{count} records</Badge>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>

        <Card className="mt-8">
          <CardHeader>
            <CardTitle className="text-lg">Executive Summary</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-sm text-slate-300 leading-relaxed">
              Maintenance activity remains on schedule with a strong focus on preventive work.
              Overdue items are actively managed through automated alerts and dispatch workflows.
              Full operational export and compliance reporting can be integrated with the hospital ERP in the next phase.
            </div>
          </CardContent>
        </Card>
      </main>
    </div>
  );
}
